import React from 'react'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';


const Details = () => {
  return (
    <div className="container mt-3">
    <h1 style={{ fontWeight: 400 }}>Detials of Sajith</h1>

    <Card sx={{ maxWidth: 600 }}>
        <CardContent>
        <div className='add_btn'>
                <button className='btn btn-primary mx-2'>update</button>
                <button className='btn btn-danger'>delete</button>
                </div>
            <div className="row">
                <div className="left_view col-lg-6 col-md-6 col-12">
                    <img src="/profile.png" style={{ width: 50 }} alt="profile" />
                    <h3 className="mt-3">Name: <span >sajith</span></h3>
                    <h3 className="mt-3">Age: <span >24</span></h3>
                    <p className="mt-3">Email: <span>sajith@gmail.com</span></p>
                    <p className="mt-3">job: <span>Doctor</span></p>
                </div>
                <div className="right_view  col-lg-6 col-md-6 col-12">
               


                    <p className="mt-5">phoneNumber: <span>+91 1234567</span></p>
                    <p className="mt-3">Address: <span>kalmunai</span></p>
                    <p className="mt-3">Spcials: <span>sss</span></p>
                </div>
            </div>

        </CardContent>
    </Card>
</div>
  )
}

export default Details